.settings-wrapper {
  width: 100%;
  height: 100%;
  max-height: calc(100vh - 100px);
  overflow: scroll;

  .flex {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }

  h1 {
    margin-bottom: 10px;
  }

  .colorpicker {
    p {
      margin-bottom: 10px;
    }
  }
}
